@use './colors.scss' as col;
@use './fonts.scss' as fonts;
@use './normalize';

body {
  font-family: fonts.$defaultFont;
  background-image: url("./Images/japanese_night_background.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}
header {
  color: white;
  font-family: fonts.$handwritingFont;
  height: 15vh;
  line-height: 15vh;
  text-align: center;
  font-size: 3em;
  background: rgb(238, 126, 141);
  background: rgb(238, 126, 217);
  background: linear-gradient(
    39deg,
    rgba(238, 126, 217, 1) 0%,
    rgba(195, 62, 62, 1) 36%,
    rgba(229, 80, 80, 1) 69%,
    rgba(220, 36, 117, 1) 100%
  );
  background-size: 400%;
  text-shadow: 0 3px 5px black;
  animation: gradient-movement 15s;
  animation-iteration-count: infinite;
  box-shadow: 0 5px 25px black;
}
@keyframes gradient-movement {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.sushi-presentation-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 100px;
  width: 100%;
  figure:hover {
    img {
      bottom: 15px;
    }
    figcaption {
      opacity: 1;
    }
  }
  figcaption {
    opacity: 0;
    margin-top: 5px;
    text-align: center;
    transition: opacity 1s;
    color: white;
    text-shadow: 1px 2px black;
    font-size: 1.2em;
    font-weight: bold;
  }
}
@media only screen and (max-width: 900px) {
  .sushi-presentation-container {
    flex-direction: column;
  }
}

.sushi-circle-presentation {
  position: relative;
  width: 300px;
  height: 300px;
  transition: border-color 0.5s;
  object-fit: cover;
  border-radius: 50%;
  border: 5px col.$rose solid;
  box-shadow: 0 5px 10px black;
  bottom: 0px;
  transition: bottom 1s;
  &:hover {
    border-color: col.$pink;
  }
}
